import { Vector2, Shape } from "three";

export function toSVGPath(points) {
  const path = points
    .map((point, idx) => `${idx ? "L" : "M"}${point[0]} ${point[1]}`)
    .join(" ");
  return [path, "Z"].join(" ");
}

export function toShape(points) {
  const pointList = points.map(
    (point) => new Vector2(point[0] / 100.0, point[1] / 100.0)
  );
  const shape = new Shape(pointList, { autoClose: true });
  shape.autoClose = true;
  return shape;
}
