const arr2vec = (arr) => ({ x: arr[0], y: arr[1] });
const vec2arr = (vec) => [vec.x, vec.y];
const Vector = {
  center: (v1, v2) => ({ x: (v1.x + v2.x) / 2, y: (v1.y + v2.y) / 2 }),
  scale: (v1, s) => ({ x: v1.x * s, y: v1.y * s }),
  add: (v1, v2) => ({ x: v1.x + v2.x, y: v1.y + v2.y }),
  sub: (v1, v2) => ({ x: v1.x - v2.x, y: v1.y - v2.y }),
  dot: (v1, v2) => ({ x: v1.x * v2.y, y: v1.x * v2.y }),
  travel: (from, to, s) =>
    Vector.add(from, Vector.scale(Vector.sub(to, from), s)),
  dist: (v1, v2) =>
    Math.sqrt(Math.pow(v2.x - v1.x, 2) + Math.pow(v2.y - v1.y, 2)),
  length: (v1) => Math.sqrt(v1.x * v1.x + v1.y * v1.y),
  rotate90: (v1) => ({ x: -v1.y, y: v1.x }),
  rotate90m: (v1) => ({ x: v1.y, y: -v1.x }),
  norm: (v1) => Vector.scale(v1, 1 / Vector.length(v1)),
};

export function makeFinger(from, to, pos, width, direction, thickness) {
  let dir = Vector.norm(Vector.sub(to, from));
  if (direction === "out") {
    dir = Vector.scale(dir, -1);
  }

  // const offset = width / Vector.dist(from, to);
  const offset = width / 100.0;

  const start = Vector.travel(to, from, pos + offset);
  const end = Vector.travel(to, from, pos - offset);
  const p0 = Vector.add(start, Vector.scale(Vector.rotate90(dir), thickness));
  const p1 = Vector.add(end, Vector.scale(Vector.rotate90(dir), thickness));

  return [vec2arr(start), vec2arr(p0), vec2arr(p1), vec2arr(end)];
}

export function makeEdge(
  sort,
  fromArr,
  toArr,
  { side, finger, thickness, doubleSheeting, isFrontBack }
) {
  const from = arr2vec(fromArr);
  const to = arr2vec(toArr);
  const dist = Vector.dist(from, to);

  const isInside = () =>
    ["right", "left"].includes(side) && (sort === 0 || sort === 2);
  const isTopBottom = () => ["top", "bottom"].includes(side);

  const direction = isTopBottom() || isInside() ? "in" : "out";

  let nFingers = Math.floor(dist / 100.0);
  if (!doubleSheeting) {
    if (sort === 2 && side !== "front") {
      nFingers = 0;
    }
  }
  const width = finger / nFingers;

  function makeFingers(n) {
    const pos = (idx) => (n - idx + 0) / (n + 1);
    return [...Array(n).keys()]
      .map((idx) => makeFinger(from, to, pos(idx), width, direction, thickness))
      .flat();
  }

  const handleOffset = ([x, y], idx) => {
    const r = thickness;
    if (isFrontBack) {
      if (sort === 1) {
        return [x - r, y];
      }
      if (sort === 3) {
        return [x + r, y];
      }
    }
    return [x, y];
  };

  const handleEdgeOffset = ([x, y], type) => {
    const r = thickness;
    if (isFrontBack) {
      if ((sort === 2 && type === "from") || (sort === 0 && type === "to")) {
        return [x - r, y];
      }
      if ((sort === 2 && type === "to") || (sort === 0 && type === "from")) {
        return [x + r, y];
      }
    }
    return [x, y];
  };

  return [
    handleEdgeOffset(vec2arr(from), "from"),
    ...makeFingers(nFingers),
    handleEdgeOffset(vec2arr(to), "to"),
  ].map(handleOffset);
}

export function makeSide(name, x, y, ctx) {
  const isFrontBack = ["front", "back"].includes(name);
  const edgeCtx = { side: name, isFrontBack, ...ctx };
  return [
    ...makeEdge(0, [0, 0], [x - 0, 0], edgeCtx),
    ...makeEdge(1, [x - 0, 0], [x - 0, y], edgeCtx),
    ...makeEdge(2, [x - 0, y], [0, y], edgeCtx),
    ...makeEdge(3, [0, y], [0, 0], edgeCtx),
  ];
}

export function makeBox({
  width,
  height,
  depth,
  doubleSheeting,
  options,
  finger = 20,
  thickness = 10,
}) {
  console.log("makeBox", { width, height, options });

  const ctx = {
    width,
    height,
    depth,
    finger,
    thickness,
    doubleSheeting,
    options,
  };

  const sides = {
    top: makeSide("top", height, width, ctx),
    bottom: makeSide("bottom", height, width, ctx),
    front: makeSide("front", height, depth, ctx),
    back: makeSide("back", height, depth, ctx),
    left: makeSide("left", depth, width, ctx),
    right: makeSide("right", depth, width, ctx),
  };

  if (!doubleSheeting) {
    delete sides.back;
  }

  return sides;
}
