import React from "react";
import { view } from "@risingstack/react-easy-state";

import Slider from "./Slider";
import Checkbox from "./Checkbox";
import Dropdown from "./Dropdown";
import { props } from "../../stores/state";

import "./controls.css";

function bindInputToStoreProp(prop) {
  return {
    value: props[prop],
    onChange: (value) => (props[prop] = value),
  };
}

const range = (f, t, s) => ({ min: f, max: t, step: s });
const availableOptions = ["none", "rabbet", "parapet"];

const Controls = view(() => {
  return (
    <div className="control-box">
      <Slider title="height" {...bindInputToStoreProp("height")} />
      <Slider title="width" {...bindInputToStoreProp("width")} />
      <Slider title="depth" {...bindInputToStoreProp("depth")} />
      <Slider
        title="thickness"
        {...bindInputToStoreProp("thickness")}
        {...range(0, 15, 1)}
      />
      <Slider
        title="finger"
        {...bindInputToStoreProp("finger")}
        {...range(0, 33, 1)}
        unit="%"
      />
      <Checkbox
        title="double Sheeting"
        {...bindInputToStoreProp("doubleSheeting")}
      />
      <Dropdown
        title="options"
        options={availableOptions}
        {...bindInputToStoreProp("options")}
      />
    </div>
  );
});

export default Controls;
