import { view } from "@risingstack/react-easy-state";
import { props } from "../stores/state";
import colormap from "../lib/colormap";

const colormapRgb = (value) =>
  `rgb(${colormap(value)
    .map((x) => x * 255.0)
    .join(",")})`;

const range = (to) => [...Array(to).keys()];

const DebugPath = view(() => {
  return (
    <div style={{ padding: "1rem" }}>
      <pre>props = {JSON.stringify(props, null, 2)}</pre>
      {/* <pre>sides = {JSON.stringify(result.sides, null, 2)}</pre> */}
      {range(11)
        .map((x) => x / 10.0)
        .map((x) => (
          <div key={x} style={{ background: colormapRgb(x) }}>
            {x}
          </div>
        ))}
    </div>
  );
});

export default DebugPath;
