import React, { useState } from "react";
import PropTypes from "prop-types";

export default function Dropdown({
  title,
  options,
  value,
  onChange,
  multiple,
}) {
  const [dropdownValue, setDropdownValue] = useState(value);

  const handleChange = (event) => {
    const newValue = !multiple
      ? event.target.value
      : Array.from(event.target.options)
          .map((option) => (option.selected ? option.value : null))
          .filter((option) => option);

    setDropdownValue(newValue);
    onChange(newValue);
  };

  return (
    <div className="control range-slider">
      <span className="control__title range-slider__title">{title}</span>
      <select multiple={multiple} value={dropdownValue} onChange={handleChange}>
        {options.map((opt) => (
          <option key={opt} value={opt}>
            {opt}
          </option>
        ))}
      </select>
    </div>
  );
}

Dropdown.propTypes = {
  onChange: PropTypes.func,
  options: PropTypes.array,
  value: PropTypes.string,
  multiple: PropTypes.bool,
};
