import React from "react";

const RangeSlider = ({
  title,
  value,
  onChange = Function.prototype,
  min = 100,
  max = 1000,
  step = 50,
  unit = "mm",
}) => {
  return (
    <div className="control range-slider">
      <span className="control__title range-slider__title">{title}</span>
      <input
        value={value}
        type="range"
        min={min}
        max={max}
        step={step}
        onChange={(event) => onChange(parseFloat(event.target.value))}
      />
      <span className="range-slider__value">
        {value} {unit}
      </span>
    </div>
  );
};

export default RangeSlider;
