import React from "react";

export default function Checkbox({ value, onChange, title, ...props }) {
  return (
    <label htmlFor={title} className="control">
      <span className="control__title">
        {title.length > 8 ? title.substring(0, 8) + "â€¦" : title}
      </span>
      <input
        type="checkbox"
        name={title}
        checked={!!value}
        onChange={(event) => onChange(event.target.checked)}
        {...props}
      />
      <span className="control__details">{title}</span>
    </label>
  );
}
