import React, { useRef, useState } from "react";
import { Canvas, useThree, useFrame, extend } from "react-three-fiber";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { result, props as boxProps } from "../stores/state";
import { toShape } from "../lib/utils";
import colormap from "../lib/colormap";
import { view } from "@risingstack/react-easy-state";

// Extend will make OrbitControls available as a JSX element called orbitControls for us to use.extend({ OrbitControls });
extend({ OrbitControls });

function Extruded({
  shape,
  color = "green",
  thickness = 0.05,
  bevelEnabled = true,
  ...props
}) {
  // This reference will give us direct access to the mesh
  const mesh = useRef();

  // Set up state for the hovered and active state
  const [active, setActive] = useState(false);

  return (
    <mesh {...props} ref={mesh} onClick={() => setActive(!active)}>
      <extrudeBufferGeometry
        args={[
          shape,
          bevelEnabled
            ? {
                depth: thickness - (2 * thickness) / 10,
                bevelEnabled: true,
                bevelSegments: 1,
                bevelOffset: -thickness / 10,
                steps: 1,
                bevelSize: thickness / 10,
                bevelThickness: thickness / 10,
              }
            : { depth: thickness, bevelEnabled: false },
        ]}
      />
      <meshLambertMaterial color={color} />
    </mesh>
  );
}

const CameraControls = () => {
  // Get a reference to the Three.js Camera, and the canvas html element.
  // We need these to setup the OrbitControls component.
  // https://threejs.org/docs/#examples/en/controls/OrbitControls

  const {
    camera,
    gl: { domElement },
  } = useThree();

  // Ref to the controls, so that we can update them on every frame using useFrame
  const controls = useRef();
  useFrame((state) => controls.current.update());
  return <orbitControls ref={controls} args={[camera, domElement]} />;
};

const rad = THREE.Math.degToRad;
const assembly = (explode = true) => {
  const h = boxProps.height / 100;
  const w = boxProps.width / 100;
  const d = boxProps.depth / 100;
  const t = boxProps.thickness / 100;
  const o = explode ? 1.0 : 0.0;
  return {
    // x (rechts), y (rauf), z (vor)
    top: { position: [d + o, 0, 0], rotation: [0, rad(90), 0] },
    bottom: { position: [-t - o, 0, 0], rotation: [0, rad(90), 0] },
    left: { position: [0, 0, -t + o], rotation: [0, 0, 0] },
    right: { position: [0, 0, -h - o], rotation: [0, 0, 0] },
    front: { position: [0, t - o, 0], rotation: [rad(90), 0, -rad(90)] },
    back: { position: [0, w + o, 0], rotation: [rad(90), 0, -rad(90)] },
  };
};

export default view(function Three() {
  const [explode, setExplode] = useState(false);
  const assemblyProps = assembly(explode);

  return (
    <div style={{ position: "relative" }}>
      <div id="info-3d">
        scroll = zoom, drag = rotate,
        <kbd>shift</kbd> + drag = pan, double-click = <b>explode</b>
      </div>
      <Canvas
        style={{ height: "100vh" }}
        onDoubleClick={() => setExplode((v) => !v)}
      >
        <CameraControls />
        <ambientLight intensity={0.2} />
        <pointLight position={[50, 100, 50]} intensity={0.8} />

        <group
          position={[
            -boxProps.depth / 200,
            -boxProps.width / 200,
            boxProps.height / 200,
          ]}
        >
          {Object.entries(result.sides).map(([name, side], idx) => (
            <Extruded
              key={name}
              {...assemblyProps[name]}
              shape={toShape(side)}
              thickness={boxProps.thickness / 100}
              color={new THREE.Color(...colormap(idx / 6))}
            />
          ))}
        </group>
      </Canvas>
    </div>
  );
});
