import React from "react";
import Flexbox from "./svg/Flexbox";
import { view } from "@risingstack/react-easy-state";
import { result } from "../stores/state";
import { toSVGPath } from "../lib/utils";

import "./svg.css";

const SVGPath = view(({ style, title, path }) => {
  const [x, y] = [0, 0];
  return (
    <g transform={`translate(${x}, ${y})`}>
      <text x="0" y="0" className="title">
        {title}
      </text>
      <g transform="translate(0, 5) scale(0.5)">
        <path d={path} {...style} />
      </g>
    </g>
  );
});

class Flexitem extends React.Component {
  render() {
    const { x = 0, y = 0, ...rest } = this.props;
    return (
      <g transform={`translate(${x}, ${y})`}>
        <SVGPath {...rest} />
      </g>
    );
  }
}
const lineStyles = {
  line1: { fill: "none", stroke: "white", strokeWidth: 0.5 },
  line2: { fill: "none", stroke: "white", strokeWidth: 1.5 },
  line3: { fill: "none", stroke: "white", strokeWidth: 2.5 },
};

export default view(function SVGBox() {
  return (
    <svg viewBox={[0, 0, 1000, 1000]}>
      <Flexbox
        style={{
          width: 1000,
          height: 1000,
          padding: 20,
          flexDirection: "row",
          justifyContent: "space-between",
          flexWrap: "wrap",
        }}
      >
        {Object.entries(result.sides).map(([name, side]) => (
          <Flexitem
            key={name}
            title={name}
            path={toSVGPath(side)}
            style={lineStyles.line2}
          />
        ))}
      </Flexbox>
    </svg>
  );
});
